.main{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: #242424;
}

.text{
    /*width: 100%;*/
    /*height: 50%;*/
    color: #ddd;
    padding: 0 5em;
    /*padding-top: 5em;*/
    font-family: 'Open Sans';
    font-size: 0.9em;
}

.title{
    font-size: 2.6em;
}

.description{
    padding-top: 30px;
    font-size: 1.8em;
    color: #aaa;
    line-height: 1.5;
    text-align: left;
}

@media only screen and (max-width: 480px){
    .main{
        flex-direction: column;
    }
    .text{
        width: 80%;
        padding: 0;
        padding-bottom: 3em;
        margin-left: 3em;
    }
}